.img-upload {
    width: 70%;
    height: 12vh;
    padding: 4%;
    border-radius: 8px;
    border: 2px solid #000;
    margin: 15px auto 8% auto;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

#img {
    opacity: 0;
    position: absolute;
    width: inherit;
    height: 100%;
    top: 0;
    left: 0;
}

.img-word {
    padding-left: 5%;
    padding-right: 5%;
    font-size: 12px;
    color: #9B9B9B;
}


