body {
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(25, 3, 228, 0.76)), to(rgba(226, 255, 139, 0.5)));
  background-image: -webkit-linear-gradient(top, rgba(25, 3, 228, 0.76), rgba(226, 255, 139, 0.5));
  background-image: -o-linear-gradient(top, rgba(25, 3, 228, 0.76), rgba(226, 255, 139, 0.5));
  background-image: linear-gradient(to bottom, rgba(25, 3, 228, 0.76), rgba(226, 255, 139, 0.5));
}



